import '../../assets/js/clipboard'
export default {
    components: {},
    data () {
        return {
            wuliu: '1231231231'
        }
    },
    methods: {
        layer(text){
            this.$vux.toast.text(text || 'hello', 'middle')
        },
        showLoading(){
            this.$vux.loading.show({
                text: ''
            })
        },
        hideLoading(){
            this.$vux.loading.hide()
        },
        copy(){
            var clipboard = new Clipboard('button');
            clipboard.on('success', e => {
                //  e.clearSelection();
                this.layer('');
            });
            clipboard.on('error', e => {
                //   e.clearSelection();
                this.layer('');
            });
        },
        gotoViewCer(){
            this.$router.push({
                path: `/viewcer?id=`
            })
        },
        sureShouhuo(){
            this.$router.push({
                path: '/received'
            })
        }
    },
    mounted() {
        this.copy();
        //  
        document.getElementsByTagName('title')[0].textContent = '';
    }
}



