
import api from '../../assets/js/api'
import dateFormat from 'vux/src/tools/date/format.js'

import {getParams} from '../../assets/js/util'
export default {
components: {},
filters: {
dateFormat
},
data () {
return {
detail: {
"money": 88,
"remark": "",
"addtime": 1,
"nid": "123123131",
"url": "",
"thawingTime": 1
},
nid: getParams()['nid'] || ''
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: ''
})
},
hideLoading(){
this.$vux.loading.hide()
},
getDetail() {
this.showLoading('')
this.$http.post(api.accountLogDetail, {
nid: this.nid
}).then(res => {
this.hideLoading()
this.detail = res.data
}).catch(e => {
this.hideLoading()
})
},
},
mounted() {
this.getDetail()
document.getElementsByTagName('title')[0].textContent = '';
}
}



